.container {
    display: flex;
    min-height: 100vh;
    background-color: var(--background);
    color: var(--foreground);
}

.mainContent {
    flex: 1;
    padding: var(--space-lg);
    max-width: 800px;
    margin: 0 auto;
    width: 100%;
}

/* Profile Header Section */
.profileHeader {
    position: relative;
    margin-bottom: var(--space-2xl);
    background-color: var(--background-light);
    border-radius: var(--radius-lg);
    overflow: hidden;
    box-shadow: var(--shadow-md);
    border: 1px solid var(--background-lighter);
}

.coverPhoto {
    height: 200px;
    background: linear-gradient(135deg, var(--background-lighter) 0%, var(--background-light) 100%);
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all var(--transition-normal);
}

.coverPhoto:hover {
    filter: brightness(1.1);
}

.coverPhoto svg {
    font-size: 1.5rem;
    margin-right: var(--space-xs);
    color: var(--foreground-dim);
}

.uploadingCoverOverlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: var(--font-semibold);
    gap: var(--space-sm);
}

.coverImg {
    height: 100%;
    width: 100%;
    object-fit: cover;
    position: absolute;
    left: 0;
    top: 0;
}

.profileInfo {
    position: relative;
    padding: var(--space-xl);
    text-align: center;
    margin-top: -60px;
}

.avatar {
    width: 120px;
    height: 120px;
    border-radius: var(--radius-full);
    background-color: var(--background-lighter);
    margin: 0 auto;
    position: relative;
    border: 4px solid var(--background-light);
    box-shadow: var(--shadow-md);
    overflow: hidden;
    cursor: pointer;
    transition: all var(--transition-normal);
}

.avatar:hover {
    transform: scale(1.05);
    box-shadow: var(--shadow-lg);
}

.avatar svg {
    position: absolute;
    bottom: 5px;
    right: 5px;
    background-color: var(--background-lighter);
    border-radius: var(--radius-full);
    padding: 5px;
    font-size: 1rem;
    color: var(--foreground);
    opacity: 0;
    transition: opacity var(--transition-fast);
}

.avatar:hover svg {
    opacity: 1;
}

.uploadingOverlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: var(--radius-full);
}

.spinner {
    animation: spin 1s linear infinite;
    font-size: 1.5rem;
    color: white;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.avatarImg {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.username {
    margin-top: var(--space-lg);
    font-size: var(--text-2xl);
    font-weight: var(--font-bold);
    color: var(--foreground);
}

.username input {
    font-size: var(--text-xl);
    padding: var(--space-sm);
    background-color: var(--background-lighter);
    border: 1px solid var(--background-lighter);
    border-radius: var(--radius-md);
    color: var(--foreground);
    width: 80%;
    max-width: 300px;
    text-align: center;
    transition: all var(--transition-fast);
}

.username input:focus {
    outline: none;
    border-color: var(--primary);
    box-shadow: 0 0 0 2px rgba(29, 161, 242, 0.2);
}

.bio {
    margin-top: var(--space-sm);
    color: var(--foreground-dim);
    font-size: var(--text-md);
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
}

.bio textarea {
    width: 100%;
    padding: var(--space-md);
    background-color: var(--background-lighter);
    border: 1px solid var(--background-lighter);
    border-radius: var(--radius-md);
    color: var(--foreground);
    font-family: var(--font-sans);
    resize: vertical;
    min-height: 100px;
    transition: all var(--transition-fast);
}

.bio textarea:focus {
    outline: none;
    border-color: var(--primary);
    box-shadow: 0 0 0 2px rgba(29, 161, 242, 0.2);
}

/* Profile Action Buttons */
.editProfileBtn {
    margin-top: var(--space-lg);
    background: var(--gradient-primary);
    color: white;
    border: none;
    border-radius: var(--radius-full);
    padding: var(--space-sm) var(--space-lg);
    font-weight: var(--font-semibold);
    font-size: var(--text-md);
    cursor: pointer;
    transition: all var(--transition-fast);
    box-shadow: var(--shadow-md);
    display: inline-flex;
    align-items: center;
    gap: var(--space-xs);
}

.editProfileBtn:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-lg);
    filter: brightness(1.1);
}

.editProfileBtn:active {
    transform: translateY(0);
    filter: brightness(0.95);
}

.editButtons {
    display: flex;
    justify-content: center;
    gap: var(--space-md);
    margin-top: var(--space-lg);
}

.saveProfileBtn {
    background: var(--gradient-primary);
    color: white;
    border: none;
    border-radius: var(--radius-full);
    padding: var(--space-sm) var(--space-lg);
    font-weight: var(--font-semibold);
    font-size: var(--text-md);
    cursor: pointer;
    transition: all var(--transition-fast);
    box-shadow: var(--shadow-md);
}

.saveProfileBtn:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-lg);
    filter: brightness(1.1);
}

.cancelBtn {
    background-color: transparent;
    color: var(--foreground-dim);
    border: 1px solid var(--foreground-muted);
    border-radius: var(--radius-full);
    padding: var(--space-sm) var(--space-lg);
    font-weight: var(--font-semibold);
    font-size: var(--text-md);
    cursor: pointer;
    transition: all var(--transition-fast);
}

.cancelBtn:hover {
    background-color: var(--background-lighter);
    color: var(--foreground);
}

/* Notification Messages */
.successMessage {
    background-color: rgba(76, 175, 80, 0.1);
    color: var(--success);
    padding: var(--space-md);
    border-radius: var(--radius-md);
    margin: var(--space-md) 0;
    text-align: center;
    animation: fadeIn 0.5s;
    border-left: 3px solid var(--success);
}

.error {
    background-color: rgba(244, 67, 54, 0.1);
    color: var(--error);
    padding: var(--space-md);
    border-radius: var(--radius-md);
    margin: var(--space-md) 0;
    text-align: center;
    border-left: 3px solid var(--error);
}

@keyframes fadeIn {
    from { opacity: 0; transform: translateY(-10px); }
    to { opacity: 1; transform: translateY(0); }
}

/* Stats Section */
.stats {
    display: flex;
    justify-content: center;
    gap: var(--space-xl);
    margin: var(--space-xl) 0;
    padding: var(--space-md) 0;
    border-top: 1px solid var(--background-lighter);
    border-bottom: 1px solid var(--background-lighter);
}

.stats > div {
    text-align: center;
}

.stats strong {
    display: block;
    font-size: var(--text-lg);
    font-weight: var(--font-bold);
    color: var(--foreground);
}

/* Action Buttons */
.actionButtons {
    display: flex;
    justify-content: center;
    gap: var(--space-md);
    margin-top: var(--space-lg);
}

.subscribeBtn {
    background: var(--gradient-secondary);
    color: white;
    border: none;
    border-radius: var(--radius-full);
    padding: var(--space-sm) var(--space-xl);
    font-weight: var(--font-semibold);
    font-size: var(--text-md);
    cursor: pointer;
    transition: all var(--transition-fast);
    box-shadow: var(--shadow-md);
}

.subscribeBtn:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-lg);
    filter: brightness(1.1);
}

.messageBtn, .bookmarkBtn {
    background-color: transparent;
    color: var(--foreground-dim);
    border: 1px solid var(--foreground-muted);
    border-radius: var(--radius-full);
    padding: var(--space-sm) var(--space-lg);
    font-weight: var(--font-medium);
    font-size: var(--text-md);
    cursor: pointer;
    transition: all var(--transition-fast);
    display: flex;
    align-items: center;
    gap: var(--space-xs);
}

.messageBtn:hover, .bookmarkBtn:hover {
    background-color: var(--background-lighter);
    color: var(--foreground);
}

/* Feed Section */
.feed {
    margin-top: var(--space-2xl);
}

.feedHeader {
    border-bottom: 1px solid var(--background-lighter);
    padding-bottom: var(--space-md);
    margin-bottom: var(--space-xl);
}

.feedHeader h3 {
    font-size: var(--text-xl);
    font-weight: var(--font-bold);
    color: var(--foreground);
}

.feedContent {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: var(--space-lg);
}

.post {
    background-color: var(--background-light);
    border-radius: var(--radius-md);
    overflow: hidden;
    box-shadow: var(--shadow-md);
    transition: all var(--transition-normal);
    border: 1px solid var(--background-lighter);
    height: 100%;
    display: flex;
    flex-direction: column;
}

.post:hover {
    transform: translateY(-5px);
    box-shadow: var(--shadow-lg);
}

.post h4 {
    padding: var(--space-md);
    font-size: var(--text-md);
    font-weight: var(--font-semibold);
    color: var(--foreground);
    margin: 0;
}

.postImage {
    width: 100%;
    height: 180px;
    object-fit: cover;
}

.postDescription {
    padding: var(--space-md);
    color: var(--foreground-dim);
    font-size: var(--text-sm);
    flex-grow: 1;
}

.noImagePlaceholder {
    width: 100%;
    height: 180px;
    background-color: var(--background-lighter);
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--foreground-muted);
}

.postMeta {
    display: flex;
    justify-content: space-between;
    padding: var(--space-sm) var(--space-md);
    font-size: var(--text-xs);
    color: var(--foreground-muted);
    background-color: var(--background-lighter);
    border-top: 1px solid var(--background-lighter);
}

.clickableTitle {
    color: var(--foreground);
    transition: color var(--transition-fast);
}

.clickableTitle:hover {
    color: var(--primary);
    text-decoration: underline;
}